import * as React from "react";

interface Props extends React.DetailedHTMLProps<React.ImgHTMLAttributes<HTMLImageElement>, HTMLImageElement> {}

interface State {
  hasError: boolean;
}

class Img extends React.Component<Props, State> {
  public constructor(props: Props | Readonly<Props>) {
    super(props);
    this.state = {
      hasError: false,
    };

    this.handleError = this.handleError.bind(this);
  }

  private handleError(event: React.SyntheticEvent<HTMLImageElement, Event>): void {
    if (!this.state.hasError) {
      this.setState({
        hasError: true,
      });
    }
  }

  public render(): React.ReactNode {
    const { onError, ...rest } = this.props;
    const { hasError } = this.state;
    return (
      <>
        {hasError ? (
          <p>
            <code>There was an problem while requesting the image.</code>
          </p>
        ) : (
          <img {...rest} onError={this.handleError} />
        )}
      </>
    );
  }
}

export { Img };
