import Markdown from "markdown-to-jsx";
import * as React from "react";
import hljs from "highlight.js";
import { dom } from "googlers-tools";
import { Img } from "../overrides/Img";

interface Props {
  style?: React.CSSProperties;
  children: string;
}

class HighlightedMarkdown extends React.Component<Props> {
  private ref: React.RefObject<HTMLDivElement>;

  public constructor(props: Props | Readonly<Props>) {
    super(props);

    this.ref = React.createRef();
  }

  public componentDidMount() {
    dom.findBy(this.ref, (ref: HTMLDivElement) => {
      ref.querySelectorAll("pre code").forEach((block: any) => {
        hljs.highlightBlock(block);
      });
    });
  }

  public render(): React.ReactNode {
    return (
      <div ref={this.ref}>
        <Markdown
          style={this.props.style}
          options={{
            forceBlock: true,
            overrides: {
              img: {
                component: Img,
              },
              // a: {
              //   component: Anchor,
              // },
              // video: {
              //   component: Video,
              // },
              // discordwidget: {
              //   component: DiscordWidget,
              // },
              // checkmark: {
              //   component: Checkmark,
              // },
              // dangermark: {
              //   component: Dangermark,
              // },
              // warnmark: {
              //   component: Warnmark,
              // },
            },
          }}
          children={this.props.children}
        />
      </div>
    );
  }
}

export { HighlightedMarkdown };
