import * as React from "react";
import { Toolbar, Page, ToolbarButton, PullHook, List } from "react-onsenui";
import { Disappear } from "react-disappear";
import Constants from "./util/Constants";
import axios from "axios";
import { Searchbar } from "./components/Searchbar";
import { Menu } from "@mui/icons-material";
import { Icon } from "./components/Icon";
import { AppList } from "./components/AppList";

interface Props extends PushProps<AppJson> {}

interface State {
  appToolbarTitle: string;
  appToolbarShadow: string;
  apps: Array<AppJson>;
  searchValue: string;
  finalSearchValue: string;
  ptrState: string;
}

class App extends React.Component<Props, State> {
  public constructor(props: any) {
    super(props);
    this.state = {
      // Initial states
      appToolbarTitle: "",
      appToolbarShadow: "noshadow",
      apps: [],
      searchValue: "",
      finalSearchValue: "",
      ptrState: "Initial",
    };

    this.renderAppCard = this.renderAppCard.bind(this);
    this.renderToolbar = this.renderToolbar.bind(this);
    this.searchFilter = this.searchFilter.bind(this);
    this.searchTrigger = this.searchTrigger.bind(this);
  }

  private searchFilter(e: any) {
    this.setState((state: Readonly<State>, props: Readonly<Props>) => ({
      searchValue: e.target.value,
    }));
  }

  private searchTrigger() {
    this.setState((state: Readonly<State>, props: Readonly<Props>) => ({
      finalSearchValue: state.searchValue,
    }));
  }

  public renderToolbar() {
    const { appToolbarTitle, appToolbarShadow } = this.state;
    return (
      <Toolbar modifier={appToolbarShadow}>
        <div className="center">{appToolbarTitle}</div>
        <div className="right">
          <ToolbarButton>
            <Icon icon={Menu} keepLight />
          </ToolbarButton>
        </div>
      </Toolbar>
    );
  }

  public componentDidMount() {
    axios
      .get("https://repo.dergoogler.com/apps.json")
      .then((response) => {
        const apps = response.data.apps;
        this.setState({
          apps: apps,
        });
      })
      .catch((error) => {
        this.setState({
          apps: [],
        });
      });
  }

  public componentWillUnmount() {
    // Remove apps array from state
    this.setState({ apps: [] });
  }

  public render(): React.ReactElement {
    return (
      <Page renderToolbar={this.renderToolbar}>
        <div>
          <Disappear
            wrapper="div"
            onDisappear={(visible) => {
              this.setState({ appToolbarShadow: visible ? "noshadow" : "" });
            }}
            style={{
              padding: "50px",
              paddingTop: "6px",
              textAlign: "center",
              backgroundColor: "#4a148c",
              color: "white",
              fontSize: "30px",
              boxShadow: "rgba(0, 0, 0, 0.3) 0px 1px 5px",
            }}
          >
              <Disappear
                wrapper="span"
                onDisappear={(visible) => {
                  this.setState({ appToolbarTitle: visible ? "" : Constants.GLobalAppName });
                }}
              >
                {Constants.GLobalAppName}
              </Disappear>
          </Disappear>
          <Searchbar
            placeholder="Search apps ..."
            onInputChange={this.searchFilter}
            onButtonClick={this.searchTrigger}
          />
          <List>
            <this.renderAppCard />
          </List>
        </div>
      </Page>
    );
  }

  private renderAppCard(): JSX.Element {
    const { apps, finalSearchValue } = this.state;

    const filteredApps = apps.filter((item) => item.prop.name.toLowerCase().includes(finalSearchValue.toLowerCase()));

    return (
      <>
        {filteredApps.map((item) => (
          <AppList pushPage={this.props.pushPage} popPage={this.props.popPage} extra={item} />
        ))}
      </>
    );
  }
}

export { App };
