import { link } from "googlers-tools";
import * as React from "react";
import { ListItem } from "react-onsenui";
import getParam from "../util/getParam";
import ViewApp from "../view/ViewApp";

interface Props extends PushProps<AppJson> {}

interface State {}

class AppList extends React.Component<Props, State> {
  public constructor(props: Props | Readonly<Props>) {
    super(props);

    this.handleViewAppClick = this.handleViewAppClick.bind(this);
  }

  public componentDidMount() {
    const { package: pkg } = this.props.extra.prop;
    const get = getParam("package");

    // Open app if url is given like http://127.0.0.1:5500/?package=com.dergoogler.mmrl
    if (pkg === get) {
      this.handleViewAppClick();
    }
  }

  private handleViewAppClick() {
    const { package: pkg, name } = this.props.extra.prop;
    // Make an fake path. Note: The page should not refreshed!
    link.setURL((set, currentPath) => {
      set(`view_${name}`, `view_${name}`, `${currentPath}/?package=${pkg}`);
    });

    this.props.pushPage({
      component: ViewApp,
      props: {
        key: "viewapp",
        extra: this.props.extra,
      },
    });
  }

  public render(): React.ReactElement<Props> {
    const { name, icon, package: pkg, versionCode, versionName, description } = this.props.extra.prop;
    const formatPackageName = `${pkg} v${versionName} (${versionCode})`;

    return (
      <>
        <ListItem onClick={this.handleViewAppClick}>
          {icon && (
            <div className="left">
              <img className="list-item__thumbnail" style={{ borderRadius: "20%" }} src={icon} />
            </div>
          )}
          <div className="center">
            <span className="list-item__title">{name}</span>
            <span className="list-item__subtitle">{description ? description : "No description"}</span>
          </div>
        </ListItem>
      </>
    );
  }
}

export { AppList };
