import * as React from "react";
import { BackButton, Button, Page, Toolbar } from "react-onsenui";
import ons from "onsenui";
import axios from "axios";
import { DownloadRounded, InstallMobileRounded } from "@mui/icons-material";
import { HighlightedMarkdown } from "../components/HighlightedMarkdown";
import { Disappear } from "react-disappear";
import AlertDialog from "../util/AlertBuilder";
import { link } from "googlers-tools";
import Toast from "../util/Toast";
import { Avatar, AvatarGroup } from "@mui/material";

interface Props extends PushProps<AppJson> {}

interface States {
  appToolbarTitle: string;
  appToolbarShadow: string;
  isDownloadDisabled: boolean;
  notes: string;
}

class ViewApp extends React.Component<Props, States> {
  public constructor(props: Props | Readonly<Props>) {
    super(props);
    this.state = {
      // Initial states
      appToolbarTitle: "",
      appToolbarShadow: "noshadow",
      isDownloadDisabled: false,
      notes: "",
    };

    this.renderToolbar = this.renderToolbar.bind(this);
    this.handleDownload = this.handleDownload.bind(this);
  }

  public componentDidMount() {
    const { readme_url, apk_url } = this.props.extra;
    const { name } = this.props.extra.prop;
    axios
      .get(readme_url)
      .then((response) => {
        this.setState({
          notes: response.data,
        });
      })
      .catch((error) => {
        if (error.response.status === 404) {
          this.setState({
            notes: `# 404: Not Found\n\n The author doesn't have created or uploaded an \`README.md\`, please try again later.\n\n\n## About Readme's\n\n- <dangermark/> readme.md\n- <checkmark/> README.md`,
          });
        }
      })
      .then(() => {
        // always executed
      });

    // Only show when the download link isn't valid
    if (!link.validURL(apk_url as string)) {
      Toast.makeText("Invalid DL-Link. Download has been disabled.", Toast.LENGTH_SHORT).show();
      this.setState({ isDownloadDisabled: true });
    }
  }

  public renderToolbar() {
    const { appToolbarTitle, appToolbarShadow } = this.state;
    const { popPage } = this.props;

    return (
      <Toolbar modifier={appToolbarShadow}>
        <div className="left">
          <BackButton onClick={popPage}>Back</BackButton>
        </div>
        <div className="center">{appToolbarTitle}</div>
      </Toolbar>
    );
  }

  public componentWillUnmount(): void {
    const { download, name, versionCode, versionName, package: pkg, icon } = this.props.extra.prop;
    // Make an fake path. Note: The page should not refreshed!
    link.setURL((set, currentPath) => {
      set(`view_${name}`, `view_${name}`, `/`);
    });
  }

  private handleDownload() {
    const { apk_url } = this.props.extra;
    apk_url ? window.open(apk_url) : null;
  }

  public render(): React.ReactNode {
    const { download, name, versionCode, versionName, package: pkg, icon } = this.props.extra.prop;
    const { contributors } = this.props.extra;

    return (
      <Page renderToolbar={this.renderToolbar}>
        <Disappear
          wrapper="div"
          onDisappear={(visible) => {
            this.setState({ appToolbarShadow: visible ? "noshadow" : "" });
          }}
          style={{
            display: "flex",
            padding: "6px 16px 16px 16px",
            textAlign: "center",
            backgroundColor: "#4a148c",
            color: "#fff",
            boxShadow: "rgba(0, 0, 0, 0.3) 0px 1px 5px",
          }}
        >
          <div style={{ display: "flex", height: "100%", width: "100%" }}>
            {icon && (
              <img
                src={icon}
                alt={name}
                style={{
                  backgroundColor: "white",
                  borderRadius: "20%",
                  width: "100px",
                  height: "100px",
                  boxShadow: "rgba(0, 0, 0, 0.3) 0px 1px 5px",
                }}
              />
            )}
            <div style={{ paddingLeft: icon ? "16px" : "0px", width: "100%" }}>
              <Disappear
                wrapper="span"
                onDisappear={(visible) => {
                  this.setState({ appToolbarTitle: visible ? "" : name });
                }}
                style={{ display: "inline-flex", fontSize: "23px", width: "100%" }}
              >
                {name}
              </Disappear>
              <span style={{ display: "inline-flex", fontSize: "16px", width: "100%" }}>
                v{versionName} ({versionCode})
              </span>
              <span
                style={{ display: "inline-flex", width: "100%", paddingTop: "18.1px", flexDirection: "row-reverse" }}
              >
                <Button
                  disabled={this.state.isDownloadDisabled}
                  style={{ backgroundColor: "#fff", color: "#4a148c", width: "50%" }}
                  onClick={this.handleDownload}
                >
                  Download
                </Button>
                {/* <span style={{ display: "inline-flex", alignItems: "center", alignSelf: "center", width: "100%" }}>
                    {pkg.length > 15 ? pkg.substring(0, 15 - 3) + "..." : pkg}
                  </span> */}
              </span>
            </div>
          </div>
        </Disappear>
        {contributors.length != 0 && (
          <div style={{ padding: "5px", display: "flex", overflow: "auto" }}>
            <AvatarGroup max={contributors.length} total={contributors.length}>
              {contributors.map((contributor) => (
                <Avatar key={contributor.name} alt={contributor.name} src={contributor.avatar} />
              ))}
            </AvatarGroup>
          </div>
        )}
        <div
          style={{ padding: "8px", height: "100%", zIndex: "-999999999", position: "relative" }}
          className="markdown-body-light"
        >
          <HighlightedMarkdown style={{ marginBottom: "8px", height: "100%" }} children={this.state.notes} />
        </div>
      </Page>
    );
  }
}

export default ViewApp;
