import ons from "onsenui";

type ToastReturn = Omit<IToast, "makeText">;

interface IToast {
  /**
   * Show the view or text notification for a short period of time.
   */
  readonly LENGTH_LONG: number;

  /**
   * Show the view or text notification for a long period of time.
   */
  readonly LENGTH_SHORT: number;

  makeText(text: string, duration: number): ToastReturn;
  show(): void;
}

class ToastClass implements IToast {
  /**
   * Show the view or text notification for a short period of time.
   */
  public readonly LENGTH_LONG: number = 5000;

  /**
   * Show the view or text notification for a long period of time.
   */
  public readonly LENGTH_SHORT: number = 2000;

  private duration: number | undefined;
  private text: string | undefined;

  public makeText(text: string, duration: number): ToastReturn {
    this.text = text;
    this.duration = duration;
    return this;
  }

  public show(): void {
    ons.notification.toast(this.text, { timeout: this.duration, animation: "fall" });
  }
}

const Toast: IToast = new ToastClass();

export default Toast;
export { ToastClass, IToast };
